export default{
	name:'matter',
	props:{
		detaildata:Object
	},
	data() {
		return {
			videoing:false,
			staticvideo:''
		}
	},
	watch:{
		detaildata(newvalue,oldvalue){
			if(newvalue.staticvideo != ''){
				console.log(newvalue.staticvideo)
				this.staticvideo = newvalue.staticvideo
				this.videoing = true
			}else{
				this.videoing = false
				// console.log('没有视频')
			}
			
		}
	}
}
